import { Component, OnInit ,ViewChildren} from '@angular/core';
import { FlashMessagesService } from 'angular2-flash-messages';

import { AuthService } from '../services/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-previous-booking',
  templateUrl: './previous-booking.component.html',
  styleUrls: ['./previous-booking.component.css']
})
export class PreviousBookingComponent implements OnInit {
	BookingData:any;
	UserData:any;
	userId:string;
	BookingDataLength:Number;
	status:string;

  constructor(private authService: AuthService,
   private router: Router,
    private flashMessageService: FlashMessagesService) { }

  ngOnInit() {
  	if(localStorage.getItem('user')!=undefined){
  		this.UserData=JSON.parse(localStorage.getItem('user'));
	  	//console.log(this.UserData);
	  	this.userId=this.UserData._id;
	  	// console.log(this.userId);
  	}  	
  	this.authService.getPrevBookingData().subscribe(booking=>{
  		// console.log(booking.data);
  		this.BookingData=booking.data;
  	})
  }
  itemClick(event){
  	// console.log(event);
  	this.status=event;
  }
  confirmBooking(item){
  	const status={
  		bookingId:item,
  		status:'Confirmed'
  	}
  	this.authService.updatePrevBookingData(status).subscribe(UpdateBooking=>{
  		if(UpdateBooking.success){
  			this.flashMessageService.show('Your Booking has been Confirmed', { cssClass: 'alert-success', timeout: 3000 });
  		}
  		this.BookingData=UpdateBooking.data;
  	})
  }
  cancelBooking(item){
  	const status={
  		bookingId:item,
  		status:'Cancelled'
  	}
  	this.authService.updatePrevBookingData(status).subscribe(UpdateBooking=>{
  		if(UpdateBooking.success){
  			this.flashMessageService.show('Your Booking has been Cancelled', { cssClass: 'alert-success', timeout: 3000 });
  		}
  		this.BookingData=UpdateBooking.data;
  	});
  }

}



